<template>
	<view>
		<!-- 顶部导航栏 -->
		<scroll-view 
			scroll-x 
			:scroll-into-view="scrollInto"
			class="scroll-row border-bottom border-white border-white"
			scroll-with-animation
			>
			<view 
				v-for="(item, index) in tabBars" :key="index" 
				class="scroll-row-item px-3 py-2 font-md"
				:id="'tab' + index"
				:class="tabIndex === index ? 'text-main font-lg font-weight-bold' : ''"
				@click="changeTab(index)">
					{{item.name}}
				</view>
		</scroll-view>
		<divider></divider>
		<!-- swiper滑动 -->
		<swiper :current="tabIndex" @change="changeSwiperTab">
			<swiper-item v-for="(item, index) in tabBars" :key="index">
				<view class="swiper-item">{{ item.name }}</view>
			</swiper-item>
		</swiper>
		<block v-for="(item, index) in list" :key="index">
			<!-- 列表样式 -->
			<common-list :item="item" :index="index" @follow="follow" @onSupport="onSupport"></common-list>
			<!-- 分割线 -->
			<divider></divider>
		</block>
	</view>
</template>

<script>
import CommonList from '@/components/common/common-list.vue';
export default {
	components: { CommonList },
	data() {
		return {
			scrollInto: "",  
			tabIndex: 0,
			tabBars:[
				{name:'关注'},
				{name:'推荐'},
				{name:'体育'},
				{name:'热点'},
				{name:'财经'},
				{name:'娱乐'},
				{name:'军事'},
				{name:'历史'},
			],
			list: [
				{
					username: '王五', // 用户名
					userpic: '/static/default.jpg', // 用户头像
					newstime: '2022-8-28', // 用户更新时间
					isFollow: false, // 是否关注
					title: 'JS高级课', // 文章标题
					titlepic: '/static/demo/datapic/27.jpg', // 文章图片
					support: {
						type: 'unsupport',
						support_count: 1,
						unsupport_count: 2
					},
					comment_count: 3,
					share_num: 2
				},
				{
					username: '李四', // 用户名
					userpic: '/static/default.jpg', // 用户头像
					newstime: '2022-8-28', // 用户更新时间
					isFollow: false, // 是否关注
					title: 'JS高级课', // 文章标题
					titlepic: '', // 文章图片
					support: {
						type: 'support',
						support_count: 1,
						unsupport_count: 2
					},
					comment_count: 3,
					share_num: 2
				},
				{
					username: '王五', // 用户名
					userpic: '/static/default.jpg', // 用户头像
					newstime: '2022-8-28', // 用户更新时间
					isFollow: false, // 是否关注
					title: 'JS高级课', // 文章标题
					titlepic: '/static/demo/datapic/27.jpg', // 文章图片
					support: {
						type: 'support',
						support_count: 1,
						unsupport_count: 2
					},
					comment_count: 3,
					share_num: 2
				}
			]
		};
	},
	onLoad() {},
	methods: {
		// 修改顶部选项卡
		changeTab(index) {
			if (this.tabIndex === index) {
				return;
			}
			this.tabIndex = index  // 如果没有高亮则证明没有点击则赋值给点击的index
			this.scrollInto = 'tab' + index // 滚动到当前点击的id
 		},
		// swiper 关联 tab栏 
		changeSwiperTab(tab) {
			this.tabIndex = tab.detail.current
		},
		follow(index) {
			this.list[index].isFollow = true;
			// 成功弹框
			uni.showToast({
				title: '关注成功'
			});
		},
		onSupport(e) {
			let item = this.list[e.index];
			let msg = e.type === 'support' ? '顶' : '踩';
			if (item.support.type === '') {
				item.support[e.type + '_count']++;
			}else if (item.support.type === 'unsupport' && e.type === 'support') {
				// 如果已经踩了 变成顶
				item.support.unsupport_count--; // 让踩-1
				item.support.support_count++; // 让顶+1
			}else if (item.support.type === 'support' && e.type === 'unsupport') {
				// 如果已经顶了 变成踩
				item.support.unsupport_count++; // 让踩+1
				item.support.support_count--	; // 让顶-1
			}
			item.support.type = e.type
			// 成功消息弹框
			uni.showToast({
				title: msg + '成功'
			})
		}
	}
};
</script>

<style></style>
